body {
    background-color: #23252C;
    font-family: 'Inter', sans-serif;
}

.container {
    margin-bottom: 3%;
    margin-top: 3%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    background-color: #1A1B21;
    width: 317px;
    border-radius: 10px;
}

.avatar {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.name-title {
    color: white;
    font-weight: 700;
    font-size: 25px;
    margin-bottom: 5px;
}

.job-name {
    color: #F3BF99;
    font-size: 13px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.website {
    text-decoration: none;
    color: white;
    font-size: 11px;
}

.website:hover {
    opacity: .8;
}

.buttons {
    display: flex;
    justify-content: space-between;
    width: 78%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-top: 20px; 
}

.button {
    width: 247px;
    border-radius: 5px;
    padding-top: 7px;
    padding-bottom: 7px;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.button:hover {
    opacity: .8;
}

.telegram {
    background-color: white;
}

.telegram img {
    width: 18px;
}

.button a {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    font-size: 14px;
    text-decoration: none;
    color: #1A1B21;
}

.text-block {
    text-align: left;
    width: 84%;
    margin-left: auto;
    margin-right: auto;
    color: white;
    margin-top: 20px;
    margin-bottom: 20px;
}

.text-block-title {
    font-weight: 700;
    font-size: 16px;
}

.text-block-text {
    font-weight: 400;
    font-size: 11px;
}

.footer {
    background-color: #161619;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.social-networks {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 20px;
    padding-bottom: 20px;
}

.social-networks a:hover {
    opacity: .8;
}